import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes} from '@angular/router';


import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { DatabindingComponent } from './databinding/databinding.component';
import { TodolistComponent } from './todolist/todolist.component';
import { NgifdirectiveComponent } from './ngifdirective/ngifdirective.component';
import { NgclassandstyledirectiveComponent } from './ngclassandstyledirective/ngclassandstyledirective.component';
import { NavbaroneComponent } from './navbarone/navbarone.component';
import { FeaturesComponent } from './features/features.component';
import { PricingComponent } from './pricing/pricing.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { PostComponent } from './post/post.component';
import { SinglepostComponent } from './singlepost/singlepost.component';
import { PostsComponent } from './posts/posts.component';

let routes: Routes=[
  {
    path: "features",
    component: FeaturesComponent
  },
  {
    path: "pricing",
    component: PricingComponent
  },
  {
    path: "posts",
    component: PostComponent
  },
  {
    path: "posts/:id",
    component: SinglepostComponent
  }

]

@NgModule({
  declarations: [
    AppComponent,
    DatabindingComponent,
    TodolistComponent,
    NgifdirectiveComponent,
    NgclassandstyledirectiveComponent,
    NavbaroneComponent,
    FeaturesComponent,
    PricingComponent,
    SidebarComponent,
    PostComponent,
    SinglepostComponent,
    PostsComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    RouterModule.forRoot(routes, {useHash : true})

  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
